/**/
/*get */
export function getResultAllAction(url, Data) {
	//token
	// let token = getToken()
	if (token) {
		defaultSetting.header = {
			// 'Authorization': token,
		}
	} else {
		defaultSetting = {}
	}
	return new Promise((resolve, reject) => {
		let myUrl;
		if (url.startsWith('http')) {
			myUrl = url;
		} else {
			myUrl = `${Api.baseUrl}${url}`;
		}
		console.log('getResultAllAction myUrl', myUrl);
		uni.request({
			...defaultSetting,
			url: myUrl,
			data: Data,
			method: 'get',
		}).then(res => {
			let result = res[1]
			console.log('result', result);
			if (result == null) {
				// Toast("none", ": myUrl=" + myUrl + " baseUrl=" + Api.baseUrl + " url=" + url);
				// Toast("none", ": url=" + url);
				Toast("none", ": " + JSON.stringify(res));
			}
			if (result.statusCode && result.statusCode == '500') {
				uni.clearStorage();
				uni.reLaunch({
					url: "/pages/login/login"
				})
			} else {
				resolve(result);
			}
		}).catch(e => {
			reject(e)
		});
	})
}
/*get*/
export function getAction(url, Data) {
	// cache('times', null)
	//token
	let token = getToken()
	//
	let deptCode = getDeptCode()
	//skey
	let skey = getSkey()
	//
	let curRoleCode = getCurRoleCode()
	if (token) {
		defaultSetting.header = {
			'Authorization': token,
			'curDeptCode': deptCode,
			'skey': skey,
			'curRoleCode': curRoleCode
		}
	} else {
		defaultSetting = {}
	}
	console.log('getAction baseUrl', baseUrl);
	return new Promise((resolve, reject) => {
		let myUrl;
		if (url.startsWith('http')) {
			myUrl = url;
		} else {
			myUrl = `${baseUrl}${url}`;
		}
		console.log('getAction myUrl', myUrl);
		uni.request({
			...defaultSetting,
			url: myUrl,
			data: Data,
			method: 'get',
		}).then(res => {
			let result = res[1];
			console.log('getAction result', result);
			if (result.statusCode && result.statusCode == '500') {
				uni.clearStorage();
				uni.reLaunch({
					url: "/pages/login/login"
				})
			} else {
				setToken(result) //token
				resolve(result.data);
			}
		}).catch(e => {
			reject(e)
		});
	})
}
/*post*/
export function postAction(url, Data, method = 'post', header = {}) {
	// cache('times', null)
	//token
	let token = getToken()
	//
	let deptCode = getDeptCode()
	//skey
	let skey = getSkey()
	//
	let curRoleCode = getCurRoleCode()
	if (token) {
		defaultSetting.header = {
			'Authorization': token,
			'curDeptCode': deptCode,
			'skey': skey,
			'curRoleCode': curRoleCode
			// "content-type": 'application/x-www-form-urlencoded'
		}
	} else {
		defaultSetting.header = {
			// "content-type": 'application/x-www-form-urlencoded'
		}
	}
	defaultSetting.header = {
		...defaultSetting.header,
		...header
	}
	let MData = {}
	return new Promise((resolve, reject) => {
		let myUrl;
		if (url.startsWith('http')) {
			myUrl = url;
		} else {
			myUrl = `${baseUrl}${url}`;
		}
		uni.request({
			...defaultSetting,
			url: myUrl,
			data: Data,
			method: method,
		}).then(res => {
			let result = res[1];
			console.log('url', myUrl);
			console.log("result==>post", result)
			if (result == undefined) {
				uni.showToast({
					icon: 'none',
					title: ''
				})
				return;
			}
			// statusCode
			if (result.statusCode && result.statusCode == '500') {
				uni.removeStorageSync('Authorization');
				uni.reLaunch({
					url: "/pages/login/login"
				})
			} else {
				setToken(result) //token
				resolve(result.data);
			}
		}).catch(res => reject(''));
	})
}
