/*file*/
export function fileAction(url, Data, method = 'post', file = [], name = '') {
	// cache('times', null)
	//token
	let token = getToken();
	//
	let deptCode = getDeptCode() ? getDeptCode() : '';
	//skey
	let skey = getSkey();
	//
	let curRoleCode = getCurRoleCode();
	console.log('fileAction token', token, 'skey', skey);
	if (token) {
		defaultSetting.header = {
			'Authorization': token,
			'curDeptCode': deptCode,
			'skey': skey,
			'curRoleCode': curRoleCode,
			// content-typeboundaryuni.uploadFilechrome
			// "content-type": 'multipart/form-data; boundary=----WebKitFormBoundaryAOihCT2p5Bt9Kjm3'
		}
	} else {
		defaultSetting.header = {
			// "content-type": 'multipart/form-data; boundary=----WebKitFormBoundaryAOihCT2p5Bt9Kjm3'
		}
	}
	let MData = {}
	return new Promise((resolve, reject) => {
		uni.uploadFile({
			...defaultSetting,
			url: `${baseUrl}${url}`,
			formData: Data,
			method: method,
			files: file,
			name: name,
			filePath: ''
		}).then(res => {
			console.log('res===>', res);
			let result = res[1]
			console.log('result===>', result);
			// statusCode
			if (result.statusCode && result.statusCode == '500') {
				/* uni.removeStorageSync('Authorization');
				uni.reLaunch({
					url: "/pages/login/login"
				})*/
			} else {
				// uni.uploadFile headersetToken
				// setToken(result)//token

				// jsonuni.uploadjson
				let data = result.data;
				if (typeof data == 'string') {
					try {
						let obj = JSON.parse(data);
						if (typeof obj == 'object' && obj) {
							data = obj;
						}
					} catch (e) {
						console.log(e);
					}
				}
				resolve(data);
			}
		}).catch(res => reject(res));
	})
}
